.about-company {
  position: relative;

  padding-top: 50px;
  padding-bottom: 110px;
  border-bottom: 1px solid $grey-dark;

  @media (max-width: $tablet-width) {
    padding-top: 26px;
    padding-bottom: 85px;
  }

  @media (max-width: $mobile-width) {
    padding-bottom: 63px;
  }
}

.about-company h2 {
  @include text-reset();
  margin-bottom: 20px;

  font-family: $title-font;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;

  @media (max-width: $tablet-width) {
    margin-bottom: 25px;

    font-size: 32px;
    line-height: 140.62%;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 21px;

    font-size: 28px;
  }
}

.about-company p {
  @include text-reset();
  margin-bottom: 21px;
  padding-right: 5px;

  @media (max-width: $tablet-width) {
    padding-right: 0;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 40px;
  }
}

.about-company span {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.about-company__btn {
  margin-top: 39px;

  @media (max-width: $mobile-width) {
    margin-top: -10px;
  }
}
