.page-header {
  position: relative;
  min-height: 100vh;
  padding-bottom: 70px;

  overflow: hidden;

  background-color: $page-header_bg;
  background-image: url("../img/bg-promo-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/bg-promo-desktop@2x.jpg");
    background-repeat: no-repeat;
    background-position: center;
  }

  @media (max-width: $tablet-width) {
    min-height: min-content;
    padding-bottom: 120px;
  }

  @media (max-width: $mobile-width) {
    padding-bottom: 70px;
  }

  &::before {
    @include pseudo();
    top: 0;
    right: 0;

    width: 100%;
    height: 100%;

    background-color: rgba($black, 0.5);
    background-repeat: no-repeat;
    background-position: center;
  }

  &--inner {
    min-height: min-content;
    padding-bottom: 0;
  }
}

.page-header--promo {
  background-image: url("../img/bg-promo-desktop@1x.jpg");

  @media (min-resolution: 2dppx) {
    background-image: url("../img/bg-promo-desktop@2x.jpg");
  }
}

.page-header__container {
  max-width: 1450px;
  margin: 0 auto;
  padding: 0 15px;
}

.page-header__wrap {
  padding-top: 20px;
  padding-bottom: 20px;

  background-color: $page-header_bg;
}

.page-header__user-nav {
  margin-left: auto;
}

.page-header__contacts {
  margin-left: auto;
}
