.promo {
  position: relative;
  padding-top: 130px;

  @media (max-width: $tablet-width) {
    padding-top: 120px;
    padding-left: 73px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 70px;
    padding-left: 0;
  }
}

.promo__about-us {
  position: relative;
  z-index: 2;

  width: 51%;

  @media (max-width: $tablet-width) {
    width: 63%;
  }

  @media (max-width: $mobile-width) {
    width: 100%;
  }
}

.promo__about-us h1 {
  @include text-reset();
  margin-bottom: 27px;

  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 70px;
  font-family: $title-font;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: $promo_text;

  @media (max-width: $tablet-width) {
    margin-bottom: 29px;

    font-size: 60px;
    line-height: 70px;
  }

  @media (max-width: $mobile-width) {
    font-size: 40px;
    line-height: 47px;
  }
}

.promo__about-us p {
  @include text-reset();
  position: relative;

  margin-bottom: 44px;
  padding-left: 65px;

  font-size: 22px;
  line-height: 34px;
  color: $promo_text;

  @media (max-width: $tablet-width) {
    margin-bottom: 55px;
    padding-left: 51px;

    font-size: 18px;
    line-height: 156.8%;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 42px;

    font-size: 16px;
  }

  &::before {
    @include pseudo();
    top: 17px;
    left: 3px;

    width: 33px;
    height: 2px;

    background-color: $promo_pseudo;

    @media (max-width: $tablet-width) {
      top: 16px;

      width: 33px;
    }

    @media (max-width: $mobile-width) {
      top: 14px;
    }
  }
}

.promo__scroll {
  display: block;
  width: min-content;
  margin-top: 186px;
  margin-left: 3px;

  font-size: 14px;
  line-height: 16px;
  color: $promo_scroll;
  white-space: nowrap;

  transform: rotate(-90deg);
  transform-origin: 0 0;
  transition: 0.3s;

  @media (max-width: $tablet-width) {
    display: none;
  }

  &:hover,
  &:focus {
    color: $promo_scroll-hover;
  }

  &:active {
    color: $promo_scroll-active;
  }
}
