.contacts-map {
  position: relative;

  padding-top: 50px;
  padding-bottom: 110px;
  border-bottom: 1px solid $grey-dark;

  @media (max-width: $tablet-width) {
    padding-top: 26px;
    padding-bottom: 85px;
  }

  @media (max-width: $mobile-width) {
    padding-bottom: 63px;
  }
}

.contacts-map h2 {
  @include text-reset();
  margin-bottom: 20px;

  font-family: $title-font;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;

  @media (max-width: $tablet-width) {
    margin-bottom: 25px;

    font-size: 32px;
    line-height: 140.62%;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 21px;

    font-size: 28px;
  }
}

.contacts-map p {
  @include text-reset();
  margin-bottom: 21px;
  padding-right: 5px;

  @media (max-width: $tablet-width) {
    padding-right: 0;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 40px;
  }
}

.contacts-map__wrap {
  position: relative;

  width: max-content;
  margin: 0 auto;
  margin-bottom: 32px;

  @media (max-width: $tablet-width) {
    margin-bottom: 17px;
  }

  @media (max-width: $mobile-width) {
    width: 100%;
    margin-bottom: 23px;
  }

  img {
    display: block;
    width: 957px;
    height: 530px;

    background-color: $black;
    object-fit: cover;

    @media (max-width: $tablet-width) {
      width: 698px;
      height: 545px;
    }

    @media (max-width: $mobile-width) {
      width: 100%;
      height: 250px;
    }
  }

  iframe {
    position: absolute;
    top: 0;
    left: 0;

    width: 957px;
    height: 530px;

    border: none;

    @media (max-width: $tablet-width) {
      width: 698px;
      height: 545px;
    }

    @media (max-width: $mobile-width) {
      width: 100%;
      height: 250px;
    }
  }
}
