.slider {
  max-width: 100%;
  margin: auto;

  @media (max-width: $tablet-width) {
    max-width: 700px;
  }

  @media (max-width: $mobile-width) {
    max-width: 300px;
  }
}

.slider--js {
  overflow: hidden;
}

.slider-line {
  margin-bottom: 30px;

  @media (max-width: $tablet-width) {
    margin-bottom: 20px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 15px;
  }
}

.slider-line--js {
  display: flex;
  transition: all ease 1s;
}

.slider img {
  display: block;
  width: 100%;
  // height: 620px;

  object-fit: cover;

  @media (max-width: $tablet-width) {
    width: 100%;
    height: 256px;
  }
}

.slider-btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
