.page-footer__container {
  max-width: 1450px;
  margin: 0 auto;
  padding: 0 15px;

  overflow-x: hidden;

  @media (max-width: $tablet-width) {
    padding: 0 25px;
  }

  @media (max-width: $mobile-width) {
    padding: 0 15px;
  }
}

.page-footer__wrap-top {
  position: relative;

  display: grid;
  grid-template-columns: minmax(202px, 30%) 1fr 337px;
  padding-top: 65px;
  padding-bottom: 59px;

  @media (max-width: $tablet-width) {
    grid-template-columns: 1fr 1fr;
    padding-top: 19px;
    padding-bottom: 37px;
  }

  @media (max-width: $mobile-width) {
    grid-template-columns: 1fr;
    padding-top: 46px;
    padding-bottom: 0;
  }

  &::after {
    @include pseudo();

    bottom: 0;
    left: calc(50% - 50vw);

    width: 100vw;
    height: 1px;

    background-color: $page-footer_line;
  }
}

.page-footer__wrap-socials {
  position: relative;

  min-width: 216px;
  padding-left: 2px;

  @media (max-width: $tablet-width) {
    grid-column-start: 1;
    grid-column-end: 3;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 38px;
    padding-bottom: 26px;
    padding-left: 0;

    &::after {
      @include pseudo();

      bottom: 0;
      left: calc(50% - 50vw);

      width: 100vw;
      height: 1px;

      background-color: $page-footer_line;
    }
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    margin-bottom: 0;
    padding-bottom: 48px;
  }
}

.page-footer__wrap-socials p {
  display: none;

  @media (max-width: $tablet-width) {
    @include text-reset();
    display: block;
    padding-top: 5px;
    padding-left: 20px;

    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $page-footer_text;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 32px;
    padding: 0;
  }
}

.page-footer__logo {
  margin-bottom: 26px;

  @media (max-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 26px;
  }
}

.page-footer__socials ul {
  @include list-reset();

  display: flex;
  flex-wrap: wrap;
  width: 260px;
  margin-bottom: -8px;

  @media (max-width: $tablet-width) {
    width: 130px;
    margin-right: -16px;
  }
}

.page-footer__socials li {
  margin-right: 16px;
  margin-bottom: 8px;
}

.page-footer__socials a {
  display: block;

  &:hover,
  &:focus {
    opacity: 0.5;
  }

  &:active {
    opacity: 1;
  }
}

.page-footer__nav {
  position: relative;

  @media (max-width: $tablet-width) {
    grid-column-start: 1;
    grid-column-end: 2;
  }

  @media (max-width: $mobile-width) {
    grid-column-start: 1;
    grid-column-end: 1;
    padding: 29px 0;

    &::after {
      @include pseudo();

      bottom: 0;
      left: calc(50% - 50vw);

      width: 100vw;
      height: 1px;

      background-color: $page-footer_line;
    }
  }
}

.page-footer__nav h3 {
  @include text-reset();

  margin-bottom: 25px;

  font-family: $title-font;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $page-footer_title;

  @media (max-width: $mobile-width) {
    margin-bottom: 0;
  }
}

.page-footer__nav ul {
  @include list-reset();

  padding-right: 60px;

  column-count: 2;

  @media (max-width: $tablet-width) {
    column-count: 1;
  }

  @media (max-width: $mobile-width) {
    margin-top: 32px;
  }
}

.page-footer__nav li {
  padding-right: 15px;

  @media (max-width: $mobile-width) {
    padding-right: 0;
  }
}

.page-footer__nav a {
  font-size: 15px;
  line-height: 25px;
  color: $page-footer_link;

  &:hover,
  &:focus {
    color: $page-footer_link-hover;
  }

  &:active {
    color: $page-footer_link-active;
  }
}

.page-footer__contacts {
  padding-right: 4px;

  color: $page-footer_text;

  @media (max-width: $tablet-width) {
    grid-column-start: 2;
    grid-column-end: 3;
    padding-right: 0;
    padding-left: 16px;
  }

  @media (max-width: $mobile-width) {
    position: relative;

    grid-column-start: 1;
    grid-column-end: 1;

    padding: 0;
    padding-top: 29px;
    padding-bottom: 25px;
  }
}

.page-footer__contacts h3 {
  @include text-reset();
  margin-bottom: 25px;

  font-family: $title-font;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $page-footer_title;

  @media (max-width: $mobile-width) {
    margin-bottom: 0;
  }
}

.page-footer__contacts ul {
  @include list-reset();

  @media (max-width: $mobile-width) {
    margin-top: 32px;
    padding-right: 10px;
  }
}

.page-footer__contacts a {
  font-size: 15px;
  line-height: 25px;
  color: $page-footer_link;

  &:hover,
  &:focus {
    color: $page-footer_link-hover;
  }

  &:active {
    color: $page-footer_link-active;
  }
}

.page-footer__wrap-bottom {
  display: flex;
  align-items: center;

  padding-top: 14px;
  padding-bottom: 14px;

  @media (max-width: $tablet-width) {
    justify-content: space-between;
    padding-top: 21px;
    padding-bottom: 24px;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    padding: 27px 0;
  }
}

.page-footer__copyright {
  display: flex;
  margin-right: 145px;
  padding-left: 2px;

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.page-footer__copyright p {
  @include text-reset();

  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $page-footer_text;
}

.page-footer__copyright p:first-child {
  margin-right: 75px;
}

.page-footer__terms {
  margin-right: 20px;

  @media (max-width: $mobile-width) {
    margin: 0;
    margin-bottom: 21px;
  }
}

.page-footer__terms a {
  display: block;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $page-footer_link;

  &:hover,
  &:focus {
    color: $page-footer_link-hover;
  }

  &:active {
    color: $page-footer_link-active;
  }
}

.page-footer__developer {
  margin-left: auto;
  padding-right: 4px;

  @media (max-width: $tablet-width) {
    padding: 0;
  }

  @media (max-width: $mobile-width) {
    margin: 0;
  }
}

.page-footer__developer a {
  display: block;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $page-footer_link;

  &:hover,
  &:focus {
    color: $page-footer_link-hover;
  }

  &:active {
    color: $page-footer_link-active;
  }
}

// Accordion

.page-footer__accordion {
  position: relative;
}

.page-footer__switch {
  display: none;

  @media (max-width: $mobile-width) {
    position: absolute;
    top: 29px;
    right: 3px;

    display: flex;
    width: 15px;
    height: 15px;
    padding: 0;

    border: none;
    background-color: transparent;

    overflow: visible;

    &::before,
    &::after {
      @include pseudo();
      background-color: $page-footer_accordion-btn;
    }

    &::before {
      margin-top: 7px;
      width: 100%;
      height: 1px;
    }

    &::after {
      display: none;

      left: 7px;
      width: 1px;
      height: 100%;
    }
  }
}

.page-footer__accordion--close ul {
  @media (max-width: $mobile-width) {
    display: none;
  }
}

.page-footer__accordion--close .page-footer__switch::after {
  display: block;
}

.page-footer__accordion--no-js .page-footer__switch {
  display: none;
}

.page-footer__accordion--no-js ul {
  @media (max-width: $mobile-width) {
    display: block;
  }
}
