.blog {
  padding-top: 88px;
  padding-bottom: 127px;

  background-color: $blog_bg;

  @media (max-width: $tablet-width) {
    padding-top: 50px;
    padding-bottom: 77px;
  }

  @media (max-width: $mobile-width) {
    padding-bottom: 64px;
  }
}

.blog h2 {
  @include text-reset();
  margin-bottom: 52px;

  font-family: $title-font;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $blog_text;

  @media (max-width: $tablet-width) {
    font-size: 32px;
    line-height: 140.62%;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 43px;
    padding-left: 10px;

    font-size: 28px;
  }
}

.blog ul {
  @include list-reset();

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;

  @media (max-width: $tablet-width) {
    grid-template-columns: 1fr;
    gap: 46px;
  }

  @media (max-width: $mobile-width) {
    gap: 30px;
  }
}

.blog__card {
  position: relative;

  display: block;
  min-height: 620px;

  color: $blog_text;

  @media (max-width: $tablet-width) {
    min-height: 256px;
  }

  &:hover,
  &:focus {
    img {
      opacity: 0.7;
    }
  }

  &:active {
    opacity: 0.5;
  }
}

.blog__card--main {
  &::before {
    @include pseudo();

    z-index: 2;
    right: 0;
    bottom: 0;

    width: 63px;
    height: 63px;

    background-image: url("../img/icon-arrow.svg");
    background-color: $accent;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
  }
}

.blog__card img {
  display: block;
  width: 100%;
  height: 620px;

  opacity: 0.5;
  object-fit: cover;

  @media (max-width: $tablet-width) {
    width: 100%;
    height: 256px;
  }
}

.blog__card h3 {
  @include text-reset();

  position: absolute;
  top: 50px;
  left: 44px;

  width: 200px;
  margin-bottom: 20px;

  font-family: $title-font;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: $blog_text;

  @media (max-width: $tablet-width) {
    top: 40px;
    left: 46px;
  }

  @media (max-width: $mobile-width) {
    top: 20px;
    left: 36px;
  }
}

.blog__card p {
  @include text-reset();

  position: absolute;
  top: 0px;
  left: 44px;

  width: 200px;
  margin-bottom: 20px;

  font-family: $title-font;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: $blog_text;

  @media (max-width: $tablet-width) {
    top: 40px;
    left: 46px;
  }

  @media (max-width: $mobile-width) {
    top: 20px;
    left: 36px;
  }
}
