.feedback {
  position: relative;

  padding-top: 90px;
  padding-bottom: 110px;

  background-color: $feedback_bg;

  @media (max-width: $tablet-width) {
    padding-top: 80px;
    padding-bottom: 103px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 30px;
    padding-bottom: 43px;
  }
}

.feedback__container {
  display: flex;

  &--inner {
    justify-content: center;
    align-items: center;
  }
}

.feedback__text {
  width: 50%;
  min-width: 570px;
  margin: 0 auto;

  @media (max-width: $tablet-width) {
    width: 100%;
    min-width: auto;
    padding: 0 67px;
  }

  @media (max-width: $mobile-width) {
    padding: 0;
  }
}

.feedback__text h2 {
  @include text-reset;

  margin-bottom: 10px;

  font-family: $title-font;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $feedback_text;

  @media (max-width: $tablet-width) {
    margin-bottom: 16px;

    font-size: 32px;
    line-height: 140.62%;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 13px;

    font-size: 28px;
  }
}

.feedback__text p {
  @include text-reset;

  margin-bottom: 50px;

  color: $feedback_text;

  @media (max-width: $tablet-width) {
    margin-bottom: 54px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 25px;
  }
}
