.logo {
  display: block;
  flex-shrink: 0;
  transition: 0.3s;

  &--header {
    padding-top: 10px;
  }

  &[href*="index.html"]:hover,
  &[href*="index.html"]:focus {
    opacity: 0.7;
  }

  &[href*="index.html"]:active {
    opacity: 0.5;
  }
}

.logo img {
  display: block;

  width: 150px;
  height: 56px;
}
