.container {
  min-width: $mobile-width-min;
  max-width: $desktop-width-max;
  margin: 0 auto;
  padding: 0 15px;

  @media (max-width: $tablet-width) {
    padding: 0 24px;
  }

  @media (max-width: $mobile-width) {
    padding: 0 15px;
  }

  &--top {
    position: relative;
    display: flex;
    // z-index: 5;

    @media (max-width: $tablet-width) {
      flex-direction: column;
    }
  }

  &--bottom {
    position: relative;
    display: flex;
    // z-index: 5;
  }
}
