.products {
  padding-top: 112px;
  padding-bottom: 55px;

  @media (max-width: $tablet-width) {
    padding-top: 54px;
    padding-bottom: 30px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 37px;
  }
}

.products h2 {
  @include text-reset();
  margin-bottom: 52px;

  font-family: $title-font;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $products_text;

  @media (max-width: $tablet-width) {
    font-size: 32px;
    line-height: 140.62%;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 43px;
    padding-left: 10px;

    font-size: 28px;
  }
}

.products ul {
  @include list-reset();

  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;

  @media (max-width: $mobile-width) {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

.products__item {
  min-height: 299px;
  padding: 40px 43px;

  border: 1px solid $products_border;
  border-left: 3px solid $products_border-left;
  background-color: $products_item-bg;

  @media (max-width: $tablet-width) {
    min-height: 232px;
    padding: 25px;
    padding-bottom: 18px;
    padding-right: 20px;
  }

  @media (max-width: $mobile-width) {
    padding-right: 12px;
    padding-bottom: 25px;
  }

  &:hover,
  &:focus {
    border-color: $products_border-hover;
  }
}

.products__item img {
  display: block;
  margin-bottom: 18px;

  fill: $products_icon;

  @media (max-width: $tablet-width) {
    width: 32px;
    height: 32px;
    margin-bottom: 10px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 13px;
  }
}

.products__item h3 {
  @include text-reset();
  margin-bottom: 15px;

  font-family: $title-font;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: $products_title;

  @media (max-width: $tablet-width) {
    margin-bottom: 9px;
  }

  @media (max-width: $mobile-width) {
    font-size: 20px;
    // исправить процент
    line-height: 140.62%;
  }
}

.products__item p {
  @include text-reset();
}

.products__btn {
  margin-top: 39px;

  @media (max-width: $tablet-width) {
    margin-top: 29px;
  }

  @media (max-width: $mobile-width) {
    margin-top: 19px;
  }
}
