.user-nav {
  display: flex;
  align-items: center;
}

.user-nav ul {
  @include list-reset();

  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  margin-right: -68px;

  @media (max-width: $tablet-width) {
    margin-right: -13px;
  }

  @media (max-width: $mobile-width) {
    margin-right: -11px;
  }
}

.user-nav li {
  margin-right: 68px;

  @media (max-width: $tablet-width) {
    margin-right: 13px;
  }

  @media (max-width: $mobile-width) {
    margin-right: 3px;
  }
}

.user-nav__link {
  display: flex;
  align-items: center;
  padding: 10px;

  line-height: 18px;
  color: $user-nav_text;

  transition: 0.3s;

  &:hover,
  &:focus {
    color: $user-nav_text-hover;
  }

  &:active {
    opacity: 0.5;
  }
}

.user-nav__link span {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.user-nav__icon {
  flex-shrink: 0;

  @media (max-width: $tablet-width) {
    .user-nav__link:hover &,
    .user-nav__link:focus & {
      opacity: 0.7;
    }
  }

  &--user {
    width: 18px;
    height: 18px;
    margin-right: 20px;

    @media (max-width: $tablet-width) {
      width: 26px;
      height: 26px;
      margin: 0;
    }
  }

  &--cart {
    width: 22px;
    height: 22px;
    margin-right: 16px;

    @media (max-width: $tablet-width) {
      width: 28px;
      height: 28px;
      margin: 0;
    }
  }
}
